    transition-duration: 1s;
    background: #191c1c;
    color: #fff;
    
} */

.state-select,.district-select,.date-picker{
    width: 100%;
    border: none;

}



.listing-wrapper{
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.listing{
    color: #F4F4F4;
    margin-top: 100px;
    background:#1A1A1A;;
    width: 99vw;
    border-radius: 124px 124px 0 0;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    font-size: larger;
    font-family: Arial, Helvetica, sans-serif;
}
.vaccination-center{
    margin: 20px 0;
    width: 80vw;
    height: 100px;
    color: #fff;
    border-radius:48px;
    border: solid;
    border-color:#fff ;
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition-duration: 0.2s;
}
.vaccination-center:hover{
    transition-duration: 0.2s;
    background:#F4F4F4;
    color:#1a1a1a;
    transform: scale(1.01);

}
.vaccination-center-name{
    flex: 2;
    padding-left: 100px;
}
.vaccination-center-slot{
flex: 1;
}
.vaccination-center-book{
flex: 0.5;
}
.vaccination-center-slot{

    display: flex;
    flex-direction:row;

}

.vaccination-center-book-text{
    cursor: pointer;
    background: -webkit-linear-gradient(rgb(77,164,255), #B275F7);
    background-clip:border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.vaccination-center-slot-number{
    color: rgb(240,113,120);
}

.date-picker{
    border-radius: 24px;
}