.transition{
    background: #000; 
    border-radius: 72px 72px 0 0;
    height: 100px;
    width: 100vw;
}

.camp-page-wrapper{
background: #000;
    height: 100vh;
    width: 100vw;
    display:flex; 
    font-family: chillax;
    font-weight: 600;
    overflow: hidden;
    
} 
.camp-left-section{
    margin-left: 30px;
    height: 100%;
    width: 70%;
}

.camp-details{
    background: #1B1B1F;
    height: 45%;
    width: 95%;
    margin-top: 30px;
    border-radius: 100px;
    display: flex;
    align-items: center;
    position: relative;
}
.camp-name{
    max-width: 50%;
    color:#fff;
    font-size: 52px;
    padding-left: 70px;
    line-height: 60px;
    background: -webkit-linear-gradient(rgb(77,164,255), #B275F7);
    background-clip:border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.camp-address{
    max-width: 50%;
    color:#A4A1FE;
    font-size: 18px;
    letter-spacing: 3px;
    line-height: 30px;
    padding-left: 70px;
    padding-right: 30px;
    background: -webkit-linear-gradient(rgb(77,164,255), #B275F7);
    background-clip:border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.left-lower{
    height:55%;
    width: 100%;
}


.camp-booking-options{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content:space-evenly;
    align-items: center;

}

.book-slot-bttn{
    transition-duration: 1s;
    position: absolute;
    bottom: 50px;
    right: 200px;
    height:10%;
    width: 10%;
    padding: 10px;
    border-radius: 48px; 
    overflow: hidden;
    background:rgb(234, 157, 182); 
    color: #000;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.book-slot-bttn:hover{
    transition-duration: 1s;
    transform: scale(1.2);
    
}

.vaccine-type,.slot-timing{
    position: relative;
    height: 85%;
    width: 40%;
    margin-right: 32px;
     border-radius: 48px; 
    overflow: hidden;
    transition-duration: 1s;
    background:#A4A1FE;   

}
.vaccine-type-text,.slot-timing-text{
    
    position: absolute;
    left: 6%;
    padding: 40px 10px 0 30px;
    color: #000;
    font-size: 52px;
    z-index: 10;
    transition-duration: 1s;
}

.vaccine-type:hover .vaccine-type-text,.slot-timing:hover .slot-timing-text{
    color:#8383e7;
    transition-duration: 1s;
}
.vaccine-type .vaccine-type-animate,.slot-timing .slot-timing-animate{
    position: absolute;
    height: 100%;
    width: 100%;
    background:#f8f8f6;
    transform: translateY(100%);
    transition-duration: 1s;
}
.vaccine-type:hover .vaccine-type-animate,.slot-timing:hover .slot-timing-animate{
    z-index: 1;
    transform: translateY(0);
    transition-duration: 1s;
} 

.selected-vaccine-type,.selected-slot{
    position: absolute;
    bottom: 10%;
    left: 10%;
    padding: 0 20px ;
    height: 20%;
    background: #191919;
    border-radius: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #A3A1FE;
    font-size: xx-large;
}

.slot-timing{
    height: 85%;
    width: 40%;
    margin-left: 32px;
    border-radius: 48px;
    background:#A4A1FE;
}
.slot-timing-text{
    padding: 40px 10px 0 30px;
    font-size: 52px;
}


.camp-right-lower-right-section{
    height: 100%;
    flex:1;
} 


.vaccine-type-dropdown{
    position: absolute;
    bottom: 0;
    background:#fff;
    height: 50%;
    width: 100%;
    z-index: 99;
    animation: fadeinup 1s;
    display: flex;
}
.covaxin,.covishield{
    flex:1;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.covaxin-container,.covishield-container{
    height: 90%;
    width: 90%;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.covaxin-text,.covishield-text{
    font-size: 1.2em;
}
.covaxin-disc,.covaxin-disc{
    width: 80%;
    font-size: 0.6em;
}
.covaxin:hover .covaxin-container,.covishield:hover .covishield-container{
    cursor: pointer;
    background: #A4A1FE;
    color: #000;
}

.covishield{
    flex:1;
    height: 100%;
    border-left: 1px solid;
    border-color:#E5E5E5;
}

.slot-timing-dropdown{
    position: absolute;
    bottom: 0;
    background:#fff;
    height: 50%;
    width: 100%;
    z-index: 99;
    animation: fadeinup 1s;
    display: flex;
    flex-direction: column;
}

.morning-slot,.noon-slot,.evening-slot{
    width: 100%;
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.morning-slot-container,.noon-slot-container,.evening-slot-container{
    width: 90%;
    height: 80%;
    border-radius: 24px;
    border: 2px solid;
    display: flex;
    justify-content: center;
    align-items: center;
}
.morning-slot:hover .morning-slot-container,.noon-slot:hover .noon-slot-container,.evening-slot:hover .evening-slot-container{
    cursor: pointer;
    background: #A4A1FE;
    color: #000;
}
.camp-right-wrapper{
    height: 100%;
    width: 30%;
    background: #000;
    display: flex;
    justify-content: center;align-items: center;
}
.camp-right{
    height: 90%;
    width: 90%;
    background: #1B1B1F;
    border-radius: 56px;
    overflow: hidden;
   
}




/* ------------------------------------------- */
@keyframes fadeinup{
    0%{
        opacity: 0;
        transform: translateY(140px);
    }

    100%{
        opacity: 1;
        transform: translateY(0px);

    }
}
